:root {
    --primary-color: #2a5b4a;
    --primary-color-dark: #21473a;
    --background-color: #f8fafc;
    --card-background: #ffffff;
    --text-color: #334155;
    --text-light: #64748b;
    --border-color: #e2e8f0;
    --font-family: 'Inter', sans-serif;
    --status-ok-bg: #f0fdf4;
    --status-ok-text: #16a34a;
    --status-baixo-bg: #fef2f2;
    --status-baixo-text: #dc2626;
    --status-vencendo-bg: #fffbeb;
    --status-vencendo-text: #d97706;
    --status-vencido-bg: #f1f5f9;
    --status-vencido-text: #64748b;
}

/* --- Cabeçalho da Página --- */
.gi-page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    flex-wrap: wrap;
    gap: 1rem;
}

.gi-search-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    flex-grow: 1;
    min-width: 250px;
}

.gi-search-icon {
    position: absolute;
    left: 0.75rem;
    color: var(--text-light);
    width: 1.25rem;
    height: 1.25rem;
}

.gi-search-input {
    width: 100%;
    padding: 0.75rem 0.75rem 0.75rem 2.5rem;
    border: 1px solid var(--border-color);
    border-radius: 0.5rem;
    font-size: 0.875rem;
}

.gi-filters-wrapper {
    display: flex;
    gap: 0.75rem;
    flex-wrap: wrap;
}

.gi-filter-button, .gi-action-button {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.625rem 1rem;
    border-radius: 0.5rem;
    border: 1px solid var(--border-color);
    background-color: var(--card-background);
    font-weight: 600;
    cursor: pointer;
    font-size: 0.875rem;
    transition: background-color 0.2s;
}

.gi-action-button {
    background-color: var(--primary-color);
    color: white;
    border-color: var(--primary-color);
}
.gi-action-button:hover {
    background-color: var(--primary-color-dark);
}

.gi-table-container {
    background-color: var(--card-background);
    border-radius: 0.75rem;
    border: 1px solid var(--border-color);
    overflow-x: auto;
}

.gi-inventory-table {
    width: 100%;
    border-collapse: collapse;
}

.gi-inventory-table th, .gi-inventory-table td {
    padding: 1rem;
    text-align: left;
    border-bottom: 1px solid var(--border-color);
    white-space: nowrap;
}

.gi-inventory-table thead tr {
    background-color: #f8fafc;
}

.gi-inventory-table th {
    font-size: 0.75rem;
    text-transform: uppercase;
    color: var(--text-light);
    font-weight: 600;
}

.gi-reagente-info {
    display: flex;
    flex-direction: column;
}

.gi-reagente-nome {
    font-weight: 500;
}

.gi-reagente-cas {
    font-size: 0.75rem;
    color: var(--text-light);
}

.gi-status-badge {
    padding: 0.25rem 0.75rem;
    border-radius: 9999px;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: capitalize;
}

.status-ok { background-color: var(--status-ok-bg); color: var(--status-ok-text); }
.status-baixo-estoque { background-color: var(--status-baixo-bg); color: var(--status-baixo-text); }
.status-vencendo { background-color: var(--status-vencendo-bg); color: var(--status-vencendo-text); }
.status-vencido { background-color: var(--status-vencido-bg); color: var(--status-vencido-text); }

.gi-actions-cell {
    display: flex;
    gap: 0.5rem;
}

.gi-table-action-button {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--text-light);
    padding: 0.25rem;
}

.gi-table-action-button:hover {
    color: var(--primary-color);
}
.action-delete:hover {
    color: var(--status-baixo-text);
}

.gi-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.gi-modal-content {
    background-color: white;
    padding: 2rem;
    border-radius: 0.75rem;
    width: 90%;
    max-width: 500px;
    box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1);
}

.gi-modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 1rem;
}

.gi-modal-header h3 {
    font-size: 1.25rem;
    font-weight: 600;
}

.gi-modal-close-button {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--text-light);
}

.gi-modal-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.gi-form-row {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 1rem;
}

.gi-form-group {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.gi-form-group label {
    font-weight: 500;
    font-size: 0.875rem;
}

.gi-form-group input, .gi-form-group select {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid var(--border-color);
    border-radius: 0.5rem;
    font-size: 0.875rem;
    box-sizing: border-box;
}

.gi-modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 0.75rem;
    margin-top: 1.5rem;
    padding-top: 1rem;
    border-top: 1px solid var(--border-color);
}

.gi-button-primary, .gi-button-secondary {
    padding: 0.625rem 1.25rem;
    border-radius: 0.5rem;
    border: 1px solid transparent;
    font-weight: 600;
    cursor: pointer;
}

.gi-button-primary {
    background-color: var(--primary-color);
    color: white;
    border-color: var(--primary-color);
}

.gi-button-secondary {
    background-color: var(--card-background);
    color: var(--text-color);
    border-color: var(--border-color);
}

