  /* --- Estilos da Barra Lateral --- */
  .sidebar {
    width: 256px;
    background-color: #ffffff;
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    height: 100vh; /* Garante que ocupe a altura toda */
    position: fixed; /* Mantém a sidebar fixa na tela */
    left: 0;
    top: 0;
    z-index: 10; /* Garante que a sidebar fique por cima de outros conteúdos */
  }

  .sidebar-header {
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #e2e8f0;
    flex-shrink: 0;
  }

  .logo-text {
    font-size: 1.5rem;
    font-weight: 700;
    color: #2a5b4a;
  }

  .sidebar-nav {
    flex-grow: 1;
    padding: 1rem;
    overflow-y: auto;
  }

  .sidebar-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }

  .nav-link {
    display: flex;
    align-items: center;
    padding: 0.75rem;
    color: #475569;
    text-decoration: none;
    border-radius: 0.5rem;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
  }

  .nav-link:hover {
    background-color: #f1f5f9;
  }

  /* Estilo para o link da página atual. 
    O NavLink adiciona a classe "active" automaticamente. 
  */
  .nav-link.active {
    background-color: #eef2ff;
    color: #2a5b4a;
    font-weight: 600;
  }

  .nav-icon {
    width: 1.25rem;
    height: 1.25rem;
    margin-right: 0.75rem;
  }

  .sidebar-footer {
    padding: 1rem;
    border-top: 1px solid #e2e8f0;
    flex-shrink: 0;
  }
